* {
  padding: 0;
  margin: 0;
}

body {
  font-family: Arial, san-serif;
}

.navigation {
  margin-bottom: 20px;
  background: linear-gradient(0deg, #0471B5 0%, #049dfc 50%, #0471B5 100%);
  box-shadow: 0 2px 4px #00000033;
}

.navigation ul {
  display: flex;
  list-style: none;
}

.navigation__link {
  display: block;
  margin: 0 20px;
  padding: 20px;

  color: #000;
  font-weight: bold;

  text-decoration: none;
  text-transform: uppercase;
}

.navigation__link:hover,
.is-active {
  background: #8dccf3;
  box-shadow: 0px 0px 10px #013c60de inset;
  border-radius: 5px;
}

.home-page-title {
  display: block;
  width: 200px;
  text-align: center;

  position: absolute;
  top: 50%;
  left: calc(50% - 100px);

  text-transform: uppercase;
}

.filter {
  margin-right: 20px;
}

.filter__input {
  margin-bottom: 20px;

  width: 200px;
  height: 30px;

  box-shadow: 0px 0px 5px #fdfeffde inset;
  border-radius: 5px;
}

.filter__selector {
  margin-left: 8px;
  width: 140px;

  border-radius: 5px;
}

.page-content {
  display: flex;
  justify-content: space-between;

  margin: 0 40px;
}

.phones-catalog {
  display: flex;
  flex-direction: column;
}

.phones-catalog h1 {
  margin: 0;

  text-align: center;
  text-transform: uppercase;
}

.phone-card {
  position: relative;

  margin: 10px 0;
  padding: 10px;

  background: #fff;
  box-shadow: 0px 0px 10px #013c60de inset;
  border-radius: 10px;
}

.phone-card a {
  text-decoration: none;
}

.phone-card__img {
  margin-right: 10px;
  max-width: 100px;
  float: left;
}

.cart-btn {
  width: 150px;
  height: 30px;

  position: absolute;
  right: 50px;
  bottom: 20px;

  background: linear-gradient(0deg, #047CC7 0%, #049dfc 50%, #047CC7 100%);
  border-radius: 5px;

  text-transform: uppercase;
  font-weight: bold;
  line-height: 30px;
}



.increased-img {
  float: left;
}

.phone__title {
  margin-bottom: 15px;
  box-shadow: 0 2px 4px #00000033;
}

.phone__description {
  margin: 10px;
}

.phone-thumbs {
  margin: 0;
  list-style: none;
}

.phone-thumbs li {
  display: inline-block;
  margin: 1em;
  background-color: white;
}

.phone-thumbs__img {
  height: 100px;
  width: 100px;
  padding: 1em;
  cursor: pointer;
}

.selected {
  box-shadow: 0px 0px 10px #013c60de inset;
  border-radius: 5px;
}


.basket-title,
.basket-text {
  text-align: center;
  text-transform: uppercase;
}

.shopping-list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
}

.shopping-list li {
  height: 300px;
  width: 200px;
  margin: 10px;

  text-align: center;
}

.shopping-list__item-link {
  text-transform: uppercase;
}

.shopping-list__img {
  max-width: 200px;
}

.remove-btn {
  margin-left: 9px;
  width: 200px;

  left: 0;
}
